<template>
  <form>
    <v-text-field
      v-model="name"
      :error-messages="nameErrors"
      :counter="10"
      label="Nombre"
      required
      @input="$v.name.$touch()"
      @blur="$v.name.$touch()"
    ></v-text-field>
    <v-text-field
      v-model="email"
      :error-messages="emailErrors"
      label="Correo electronico"
      required
      @input="$v.email.$touch()"
      @blur="$v.email.$touch()"
    ></v-text-field>
    <v-textarea
      v-model="message"
      :error-messages="messageErrors"
      :counter="255"
      label="Mensaje"
      required
      @input="$v.message.$touch()"
      @blur="$v.message.$touch()"
    ></v-textarea>

    <v-btn class="mr-4" color="primary" @click="submit">Enviar</v-btn>
    <v-btn @click="clear">Limpiar</v-btn>
  </form>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, maxLength, email } from "vuelidate/lib/validators";

export default {
  mixins: [validationMixin],

  validations: {
    name: { required, maxLength: maxLength(10) },
    email: { required, email },
    message: { required, maxLength: maxLength(255) },
  },

  data: () => ({
    name: "",
    email: "",
    message: "",
  }),

  computed: {
    nameErrors() {
      const errors = [];
      if (!this.$v.name.$dirty) return errors;
      !this.$v.name.maxLength &&
        errors.push("El nombre no puede exceder los 10 caracteres");
      !this.$v.name.required && errors.push("El nombre es un campo obligatorio");
      return errors;
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.email.$dirty) return errors;
      !this.$v.email.email && errors.push("Debe ingresar un correo válido");
      !this.$v.email.required && errors.push("El correo es un campo obligatorio.");
      return errors;
    },
    messageErrors() {
      const errors = [];
      if (!this.$v.message.$dirty) return errors;
      !this.$v.message.maxLength && errors.push("El mensaje no debe exceder los 255 caracteres");
      !this.$v.message.required && errors.push("El mensaje de contacto es obligatorio.");
      return errors;
    }
  },

  methods: {
    submit() {
      this.$v.$touch();
    },
    clear() {
      this.$v.$reset();
      this.name = "";
      this.email = "";
      this.message = "";
    }
  }
};
</script>

