<template>
  <div>
    <v-lazy
      v-model="isActive"
      :options="{
          threshold: 0.1
        }"
      min-height="200"
      transition="fade-transition"
      v-for="(item, i) in $store.state.avatarCards"
      :key="i"
      class="d-flex flex-column float-left"
      style="margin-left: 4%; margin-top: 2%;"
    >
      <v-card class="mx-auto" max-width="330" outlined>
        <v-list-item three-line>
          <v-list-item-content>
            <div class="overline mb-4">Servicio: {{ item.tipo }}</div>
            <v-list-item-title class="headline font-weight-light mb-3">{{ item.title }}</v-list-item-title>
            <v-list-item-subtitle>{{ item.text }}</v-list-item-subtitle>
          </v-list-item-content>

          <v-list-item-avatar tile size="80">
            <v-icon :color="item.iconcolor">{{item.icon}}</v-icon>
          </v-list-item-avatar>
        </v-list-item>

        <v-card-actions>
          <v-btn
            text
            color="primary"
            @click="showDialog(true, item.dialogTitle, item.dialogImage, item.text, item.iconcolor, item.icon)"
          >Más info</v-btn>
        </v-card-actions>
      </v-card>
    </v-lazy>
    <v-dialog v-model="dialog" max-width="500px">
      <v-card max-width="500" class="mx-auto">
        <v-list-item>
          <v-list-item-avatar>
            <v-icon :color="iconcolor">{{icon}}</v-icon>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title class="headline">{{title}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-img :src="image" height="200" style="margin-bottom: 10px;"></v-img>
        
        <v-card-text>{{body}}</v-card-text>

        <v-card-actions>
          <v-btn text color="primary" @click="dialog=false">Cerrar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isActive: false,
      dialog: false,
      title: "",
      image: "",
      body: "",
      icon: "",
      iconcolor: "",
    };
  },
  methods: {
    showDialog(dialog, title, image, body, iconcolor, icon) {
      this.dialog = dialog;
      this.title = title;
      this.image = image;
      this.body = body;
      this.icon = icon;
      this.iconcolor = iconcolor;
    }
  }
};
</script>

