    >
      <v-card flat tile class="secondary lighten-1 white--text text-center">
        <v-card-text>
          <v-btn v-for="icon in $store.state.footerIcons" :key="icon" class="mx-4 white--text" icon>
            <v-icon size="24px">{{ icon }}</v-icon>
          </v-btn>
        </v-card-text>

        <v-card-text class="white--text pt-0">
          {{$store.state.footerDescription}}
        </v-card-text>

        <v-divider></v-divider>

        <v-card-text class="white--text">
          
          {{ new Date().getFullYear() }} —
          <strong>Barza constructores</strong>
        </v-card-text>
      </v-card>
    </v-footer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      windowSize: {
        x: 0,
        y: 0
      }
    };
  },
  mounted() {
    this.onResize();
  },

  methods: {
    onResize() {
      this.windowSize = { x: window.innerWidth, y: window.innerHeight };
    }
  }
};
</script>

